.game-page .game-contents .action-panel {
    display: flex;
    justify-content: space-between;
}
.game-page .game-contents .action-panel button {
    height: 40px;
    background-image: linear-gradient(to top, #ff409a, #c438ef);
    color: white;
    border-radius: 40px;
    padding: 0 20px;
    font-weight: 600;
}
.game-page .game-contents .action-panel button:disabled {
    background: white;
    color: #747f8d;
    border: 1px solid #d3d7db;
}
.game-page .game-contents .radio-button-group button.radio-button {
    background-color: rgba(242, 243, 245, 0.4);
    border-radius: 8px;
    height: 40px;
    color: #4f5660;
    font-size: 16px;
    font-weight: normal;
    margin-right: 24px;
    border: 0;
}
.game-page .game-contents .radio-button-group button.radio-button::before {
    width: 23px;
    height: 23px;
    content: ' ';
    display: inline-block;
    margin-right: 10px;
    vertical-align: middle;
    border-radius: 50%;
    border: 1px solid #747f8d;
}
.game-page .game-contents .radio-button-group button.radio-button.selected::before {
    background-color: #ffb000;
    border: 1px solid #ffb000;
    box-shadow: 0 0 0px 7px #fff inset;
}
input {
    border-radius: 8px !important;
    outline: 0;
    padding-left: 8px;
    border-width: 1px;
}
