.game-page .game-contents .game-info-panel .box-prizes {
    font-size: 16px;
    font-weight: 600;
    background-color: #f2f3f5;
    padding: 20px;
    border-radius: 4px;
    word-break: break-all;
    line-height: 24px;
}
.game-page .game-contents .game-info-panel .box-prizes span {
    color: #43b581;
    margin-right: 16px;
    padding: 5px;
    border-radius: 3px;
}
.game-page .game-contents .game-info-panel .box-prizes span.opened {
    position: relative;
    background-color: #898a8d;
}
.game-page .game-contents .game-info-panel .box-prizes span.opened::before {
    content: '';
    position: absolute;
    font-size: 12px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.game-page .game-contents .game-info-panel .create-box-panel {
    display: flex;
    align-items: center;
    margin-top: 32px;
}
.game-page .game-contents .game-info-panel .create-box-panel .amounts-panel {
    display: flex;
}
.game-page .game-contents .game-info-panel .create-box-panel .edit-amount-panel {
    margin: 0 16px 0 0;
}
.game-page .game-contents .game-info-panel .create-box-panel button {
    margin: 0;
}
.game-page .game-contents .game-info-panel .create-box-panel a.btn-empty-boxes {
    font-size: 16px;
    margin-left: 16px;
    color: #ffb000;
    cursor: pointer;
    text-decoration: underline;
}
.game-page .game-contents.mystery-box-result-contents {
    width: 550px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
