    line-height: 17px;
    color: #747f8d;
    margin-top: 16px;
}
.game-page .game-contents .game-info-panel {
    min-height: 200px;
}
.game-page .game-contents .game-info-panel button {
    background: white;
    border: 1px solid #d3d7db;
    border-radius: 8px;
    font-size: 16px;
    color: #747f8d;
    font-weight: 500;
    margin-right: 32px;
}
.game-page .game-contents .game-info-panel button.other {
